<script>
export default {
  data() {
    return {
      prod_img: "https://arrgrr.github.io/pinjet/img/prod.png",
      socket_item: [
        { name: "Pogo Socket", linkpath: "/en/product/pogo_socket" },
        { name: "Rolling Socket", linkpath: "/en/product/rolling_socket" },
        { name: "POP Socket", linkpath: "/en/product/pop_socket" },
        { name: "Burn-in Socket", linkpath: "/en/product/burn_in_socket" },
        { name: "PSR", linkpath: "/en/product/psr" }
      ],
      changeKit_item: [
        {
          name: "Pick & Place,SLT Model",
          linkpath: "/en/product/pick&place_slt_model"
        },
        {
          name: "Bowl Feeder Model",
          linkpath: "/en/product/bowl_feeder_model"
        },
        { name: "Pogo Tower", linkpath: "/en/product/pogo_tower" }
      ],
      ft_item: [
        { name: "Parts", linkpath: "/en/product/parts" },
        { name: "Clean Unit", linkpath: "/en/product/clean_unit" },
        { name: "Laser Cleaner", linkpath: "/en/product/laser_cleaner" },
        { name: "Static Cleaner", linkpath: "/en/product/static_clean" },
        { name: "Socket Brush", linkpath: "/en/product/clean_brush" },
        {
          name: "Anti Sticky Coating",
          linkpath: "/en/product/anti_sticky_coating"
        },
        { name: "ESD Coating", linkpath: "/en/product/esd_coating" }
      ]
    };
  },
  methods: {
    expand_socket: function(e) {
      var list1 = document.getElementById("list_socket");
      var list2 = document.getElementById("list_change");
      var list3 = document.getElementById("list_ft");
      var li_h = 32;
      var li_num = this.socket_item.length;
      list1.style.height = li_h * li_num + "px";
      list2.style.height = "0";
      list3.style.height = "0";
      var mmm = document.getElementsByClassName("menu");
      for (var m = 0; m < mmm.length; m++) {
        mmm[m].classList.remove("menu_active");
      }
      e.target.classList.add("menu_active");
    },
    expand_change: function(e) {
      var list1 = document.getElementById("list_socket");
      var list2 = document.getElementById("list_change");
      var list3 = document.getElementById("list_ft");
      var li_h = 32;
      var li_num = this.changeKit_item.length;
      list2.style.height = li_h * li_num + "px";
      list1.style.height = "0";
      list3.style.height = "0";
      var mmm = document.getElementsByClassName("menu");
      for (var m = 0; m < mmm.length; m++) {
        mmm[m].classList.remove("menu_active");
      }
      e.target.classList.add("menu_active");
    },
    expand_ft: function(e) {
      var list1 = document.getElementById("list_socket");
      var list2 = document.getElementById("list_change");
      var list3 = document.getElementById("list_ft");
      var li_h = 32;
      var li_num = this.ft_item.length;
      list3.style.height = li_h * li_num + "px";
      list1.style.height = "0";
      list2.style.height = "0";
      var mmm = document.getElementsByClassName("menu");
      for (var m = 0; m < mmm.length; m++) {
        mmm[m].classList.remove("menu_active");
      }
      e.target.classList.add("menu_active");
    },
    put_content: function(e) {
      var ggg = document.getElementsByClassName("p_items");
      if (e.target.classList.value.includes("p_items")) {
        for (var d = 0; d < ggg.length; d++) {
          ggg[d].classList.remove("p_items_active");
        }
        e.target.classList.add("p_items_active");
      } else if (e.target.classList.value.includes("menu")) {
        var mmm = document.getElementsByClassName("menu");
        for (var m = 0; m < mmm.length; m++) {
          mmm[m].classList.remove("menu_active");
        }
        e.target.classList.add("menu_active");

        var list1 = document.getElementById("list_socket");
        var list2 = document.getElementById("list_change");
        var list3 = document.getElementById("list_ft");
        list1.style.height = "0";
        list2.style.height = "0";
        list3.style.height = "0";
      }
    }
  }
};
</script>

