export const img_roller = {
    methods: {
        clock_wise: function () {
            var cc = document.getElementById("now_img").getAttribute("src");
            for (var i = 0; i < this.prod_img.length - 1; i++) {
                this.current = i + 2
                if (this.prod_img[i] == cc) {

                    document
                        .getElementById("now_img")
                        .setAttribute("src", this.prod_img[i + 1]);
                }
            }
        },
        anti_clock_wise: function () {
            var cc = document.getElementById("now_img").getAttribute("src");
            for (var i = this.prod_img.length; i > 0; i--) {
                this.current = i
                if (this.prod_img[i] == cc) {
                    document
                        .getElementById("now_img")
                        .setAttribute("src", this.prod_img[i - 1]);
                }
            }
        }
    }
}