<template>
  <div class="home">
    <div class="home_container">
      <div class="title">
        <h1 class="slogan">品質 技術 創新</h1>
        <p class="sub_title">培養各領域專業，配合客戶各種不同的需求發展先進的產品與技術，迎合市場日趨嚴苛的要求。</p>
      </div>
      <div class="prod_roller">
        <div class="roller">
          <div @click="anticlock_wise" id="nav_l">
            <font-awesome-icon class="icon" :icon="['fal', 'angle-left']" />
          </div>
          <div @click="clock_wise" id="nav_r">
            <font-awesome-icon class="icon" :icon="['fal', 'angle-right']" />
          </div>
          <div id="player" class="roller_container">
            <router-link to="/product" id="top1" class="top1"></router-link>
            <router-link to="/product/laser_cleaner" id="top2" class="top2"></router-link>
            <router-link to="/product/pick&place_slt_model" id="top3" class="top3"></router-link>
            <router-link to="/product/probe_head" id="top4" class="top4"></router-link>
            <router-link to="/product/pogo_socket" id="top5" class="top5"></router-link>
          </div>
        </div>
        <div class="prod_discription">
          <div id="prod_name" class="prod_name">{{prods[0].name}}</div>
          <div id="prod_info" class="prod_info">{{prods[0].info}}</div>
          <router-link id="prod_goto" :to="where" class="cta">
            產品列表
            <font-awesome-icon class="icon" :icon="['fal', 'arrow-right']" />
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      where: "/product/pogo_pin",
      prods: [
        {
          name: "POGO PIN",
          info: "系列產品描述1系列產品描述1系列產品描述1",
          linkpath: "/product"
        },

        {
          name: "FT ACCESSORIES",
          info: "系列產品描述5",
          linkpath: "/product/parts"
        },
        {
          name: "CHANGE KIT",
          info: "系列產品描述4",
          linkpath: "/product/pick&place_slt_model"
        },
        {
          name: "PROBE HEAD",
          info: "系列產品描述3",
          linkpath: "/product/probe_head"
        },
        {
          name: "SOCKET",
          info: "系列產品描述2",
          linkpath: "/product/pogo_socket"
        }
      ]
    };
  },
  methods: {
    clock_wise: function() {
      var nav_r = document.querySelectorAll("#player div ");
      var nowfront =
        parseInt(
          document
            .getElementsByClassName("top5")[0]
            .getAttribute("id")
            .charAt(3)
        ) - 1;
      document.getElementById("prod_name").textContent = this.prods[
        nowfront
      ].name;
      document.getElementById("prod_info").textContent = this.prods[
        nowfront
      ].info;
      this.where = this.prods[nowfront].linkpath;
      for (var i = 0; i < nav_r.length; i++) {
        var nowClass = parseInt(nav_r[i].className.charAt(3));

        if (nowClass < 5) {
          nav_r[i].setAttribute("class", "top" + (nowClass + 1));
        } else {
          nav_r[i].setAttribute("class", "top" + (nowClass - 4));
        }
      }
    },
    anticlock_wise: function() {
      var nav_r = document.querySelectorAll("#player div ");
      var nowfront =
        parseInt(
          document
            .getElementsByClassName("top2")[0]
            .getAttribute("id")
            .charAt(3)
        ) - 1;
      this.where = this.prods[nowfront].linkpath;
      document.getElementById("prod_name").textContent = this.prods[
        nowfront
      ].name;
      document.getElementById("prod_info").textContent = this.prods[
        nowfront
      ].info;
      for (var i = 0; i < nav_r.length; i++) {
        var nowClass = parseInt(nav_r[i].className.charAt(3));
        if (nowClass > 1) {
          nav_r[i].setAttribute("class", "top" + (nowClass - 1));
        } else {
          nav_r[i].setAttribute("class", "top" + (nowClass + 4));
        }
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../css/home.scss";
</style>


